body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;

  background-color: cornsilk;
  height: 100%;
  width: 100%;
}

footer {
  position: fixed;
  bottom: 0px;
  width: 100%;
  height: 50px;
  padding-top: 7px;

  border-radius: 5px;
  border-top: 2px solid green;
  -webkit-box-shadow: 0 0 10px yellowgreen;
          box-shadow: 0 0 10px yellowgreen;
  background-color: cornsilk;
}

footer > .contact {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 0px 70px;
}

footer > .contact > .social-block {
  width: 30%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

footer a {
  font-size: 19px;
  color: black;
  border: 2px solid transparent;
  padding: 0 15px 0 15px;
}

footer a:hover {
  color: black;
  text-decoration: none;
  border: 2px solid yellowgreen;
  border-radius: 5px;
  -webkit-box-shadow: 0 0 10px yellowgreen;
          box-shadow: 0 0 10px yellowgreen;
}

.alert {
    -webkit-box-shadow: 0 0 20px red;
            box-shadow: 0 0 20px red;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
}


@media screen and (max-width: 450px) {
  footer {
    height: 70px;
    width: 400px;
  }

  footer > .contact {
    margin: 0 0 0 20px;
    text-align: center;
  }

  footer > .contact > .social-block {
    width: auto;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: end;
        justify-content: flex-end;
  }

}


