.modal-window {
    width: 1000px;
    border: 1px solid yellowgreen;
    -webkit-box-shadow: 0 0 15px green;
            box-shadow: 0 0 15px green;

    margin-bottom: 75px;
    padding: 25px;
    border-radius: 15px;
}

.modal-window.lose {
    border: 1px solid red;
    -webkit-box-shadow: 0 0 15px red;
            box-shadow: 0 0 15px red;
}

.modal-window h1 {
    text-align: center;
}

.modal-window .button-block {
    width: 700px;
    margin: 0 auto;
    padding-top: 25px;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.modal-window .button-block a, .modal-window .button-block a:hover {
    text-decoration: none;
    color: black;
}


